@mixin arrowButton() {
  cursor: pointer;
  height: rem(20px);
  width: rem(20px);
  top: rem(2.54px);
  left: (2.25px);
}

.skyplus-carousel {
  &__header {
    display: flex;
    padding-bottom: rem(16px);
    justify-content: space-between;
  }

  &__header-title {
    display: flex;
    color: var(--text-primary);
    font-size: rem(18px);

    @include media('>=tablet') {
      font-size: rem(24px);
    }
  }

  &__header-icon {
    display: flex;
    justify-content: space-between;
    padding: rem(0px) rem(20px) rem(0px) rem(20px);

    &--left {
      @include arrowButton();
    }

    &--right {
      @include arrowButton();
    }

    &--right:focus,
    &--left:focus {
      outline: 1.5px dotted var(--primary-main);
    }
  }

  &__slider-container {
    position: relative;
    width: 100%;
    overflow: hidden;
  }

  &__slider-wrapper {
    display: flex;
    transition: transform 1s ease;
    width: 100%;
  }

  &__slide {
    display: block;
    margin-left: auto;
    margin-right: auto;
    flex: 0 0 100%;
    width: 100%;
    border-radius: rem(4px);
    width: 100%;
    height: auto;

    @include media('>=tablet') {
      flex: 0 0 78%;
      margin-left: 2%;
      border-radius: rem(4px);
    }
  }

  &__dots-container {
    text-align: center;
  }

  &__dot {
    display: inline-block;
    height: rem(8px);
    width: rem(8px);
    margin: 0 rem(2px);
    border-color: #eae8e8;
    background-color: #e2ebf2;
    border-radius: 50%;
    cursor: pointer;

    &--active {
      width: rem(32px);
      height: rem(8px);
      border-radius: rem(6px);
      background-color: #000099;
    }
  }
}

.addon-carousel {
  & .skyplus-carousel {
    &__header-icon {
      &--left,
      &--right {
        & span {
          &::before {
            display: inline-block;
            height: rem(24px);
            width: rem(24px);
            justify-content: center;
            flex-wrap: wrap;
            align-content: center;
            color: var(--primary-main);
            font-size: rem(24px);
          }
        }
      }

      &--left {
        margin-right: rem(8px);
        & span:hover {
          &::before {
            content: '\e96e';
          }
        }
      }

      &--right {
        margin-right: rem(8px);
        & span:hover {
          &::before {
            content: '\e973';
          }
        }
      }
    }

    &__slide {
      @include media('>=tablet') {
        flex: 0 0 49%;
        margin-left: 0;
        margin-right: rem(8px);
      }

      & .skyplus-addon-card {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: end;
        height: rem(366px);

        &__image-container {
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          z-index: -1;

          & img {
            width: 100%;
            height: 100%;
            border-radius: 1rem;
          }
        }
        &__description {
          padding: rem(16px) rem(16px) rem(12px) rem(16px);
          border-radius: rem(16px);
          background: rgba(38, 48, 78, 0.35);
          backdrop-filter: blur(rem(16px));
          margin: rem(20px) rem(24px);

          &-header {
            color: var(--white);
            font-size: 18px;
            font-style: normal;
            font-weight: 500;
            line-height: rem(16px);
            margin-bottom: rem(16px);
          }

          &-passenger {
            @include body-small-regular;
            color: var(--secondary-white);
            font-size: rem(12px);
            line-height: rem(18px);
            margin-bottom: rem(4px);
          }

          &-price {
            display: flex;
            gap: rem(4px);
            align-items: baseline;

            & .strike-price {
              @include body-small-regular;
              color: var(--secondary-deep);
              text-decoration-line: line-through;
            }
            // ToDo :- Need to check the typography
            & .price {
              color: var(--accent-main);
              font-size: rem(24px);
              font-weight: 600;
              line-height: rem(30px);
            }
          }

          &-button {
            position: absolute;
            right: rem(16px);
            bottom: rem(12px);

            & .skyplus-button {
              & button {
                padding: rem(6px) rem(20px);
                font-size: rem(16px);
                font-weight: 500;
                line-height: rem(24px);
              }
            }
          }
        }
      }
    }
  }
}
