.radio-label {
  color: get-color(text-secondary);
  font-size: rem(14px);
  font-weight: 400;
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  line-height: rem(20px);
  padding: rem(10px) 0;

  .disabled-label {
    color: get-color(text-disabled);
  }
}

.radio-label.checked {
  color: get-color(primary-main);
}

.radio-input {
  margin: 0;
  visibility: hidden;
}

.radio-input:checked + span::after {
  opacity: 1;
}

.custom-radio {
  left: rem(-8px);
  cursor: pointer;
  width: rem(20px);
  height: rem(20px);
  border: rem(1.5px) solid get-color(sel-con-selected-background);
  border-radius: 50%;
  display: inline-block;
  position: relative;

  &:focus-visible {
    outline: rem(1px) solid get-color(secondary-main);
    outline-offset: rem(2px);
  }
}

/* for inner filled circle */
.custom-radio::after {
  content: '';
  width: rem(10px);
  height: rem(10px);
  background: get-color(sel-con-selected-background);
  position: absolute;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity 0.2s;
}

.input-radio-disabled {
  pointer-events: none;
  border-color: get-color(text-disabled);
}
