@import '../../common/scss/utils';
@import '../../common/scss/main';

.form-checkbox {
  cursor: pointer;

  input[type='checkbox'] {
    appearance: none;
    outline: rem(2) solid var(--primary-main);
    height: rem(16);
    min-width: rem(16);
    border-radius: rem(4);

    &::before {
      visibility: hidden;
      content: '✓';
      font-size: rem(16);
      color: var(--white);
    }
  }

  &--disabled {
    &.form-checkbox {
      cursor: initial;
    }

    input[type='checkbox'] {
      outline: rem(2) solid var(--text-disabled) !important;
    }
  }

  input[type='checkbox']:checked {
    background-color: var(--primary-main) !important;

    &::before {
      visibility: visible !important;
    }
  }

  &--disabled input[type='checkbox']:checked {
    background-color: var(--btn-disabled-background) !important;

    &::before {
      visibility: visible !important;
    }
  }
}
